.header {
  background-color: $bg-color;
}

.header__navigation {
  display: flex;
  align-items: flex-start;
  padding-top: 20px;

  @extend %wrapper;
}

@media (max-width: $mobile-width) {
  .header__navigation {
    align-items: center;
    justify-content: space-between;
    padding-bottom: 62px;
    flex-wrap: wrap;
  }
}
