.profitability {
  background-color: $bg-color;
  padding: 70px 0 80px;
}

.profitability__title {
  font-weight: normal;
  font-size: 42px;
  text-align: center;
  margin: 0 0 30px;
}

.profitability__list,
.profitability__grafics {
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  list-style: none;
}

.profitability__list-item {
  padding-left: 20px;
  margin-bottom: 40px;
  min-width: 260px;
  background: url("../img/check.svg") no-repeat top left;
}

.profitability__grafics {
  text-align: center;
}

.profitability__grafic-name {
  font-weight: bold;
  font-size: 30px;
  max-width: 295px;
  margin: 0 auto;
}

.profitability__grafic-percent {
  font-weight: bold;
  font-size: 42px;
  margin: 0 0 26px;
}

.profitability__grafic-discr {
  margin: 0;

  &::before {
    content: "";
    width: 27px;
    height: 27px;
    display: block;
    background: url("../img/many.png");
    margin: 0 auto 5px;
  }
}

.profitability__grafic-lup {
  margin: 20px auto 0;
  width: 20px;
  height: 20px;
  background: url("../img/Shape.png");
  display: block;
}

.profitability__footer {
  text-align: center;
  margin-top: 10px;
}

@media (max-width: $mobile-width) {
  .profitability__list {
    flex-wrap: wrap;
    margin-bottom: 10px;
  }

  .profitability__list-item {
    margin-bottom: 20px;
  }

  .profitability__grafics-wrapper {
    overflow-x: scroll;
  }

  .profitability__grafics {
    width: 610px;
  }

  .profitability__grafic {
    width: 286px;
    background: #fff;
    box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.1);
    margin-right: 20px;
    padding: 30px 10px;
  }

  .profitability__grafic-name {
    font-size: 15px;
  }

  .profitability__grafic-percent {
    font-size: 24px;
  }

  .profitability__grafic-discr {
    width: 156px;
    font-size: 13px;
    margin: 5px auto;
  }

  .profitability__footer {
    display: none;
  }
}
