.top-menu {
  width: 160px;
  margin-top: 25px;
}

.top-menu__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.lang {
  flex: 1 1 auto;
  text-align: right;
  padding: 0 10px;
  border-right: 1px solid $text-color;
}

.link-icon {
  text-decoration: none;
}

.top-menu__icon {
  fill: $text-color;
  margin-right: 28px;
  transition: 0.3s;

  &:hover {
    fill: $sec-color;
  }
}

.top-menu__social {
  margin-top: 40px;
  display: none;
  vertical-align: baseline;

  & .top-menu__icon {
    margin-right: 0;
  }

  & .link-icon:not(:last-of-type) {
    margin-right: 25px;
  }
}

.phone {
  display: none;
}

@media (max-width: $mobile-width) {
  .mobile-menu {
    display: none;
  }

  .top-menu {
    width: 100%;
    text-align: center;
    margin-top: 0;
  }

  .top-menu__wrapper {
    justify-content: center;
  }

  .top-menu__icon {
    margin-right: 0;
  }

  .phone {
    display: block;
    font-size: 14px;
  }

  .top-menu__phone {
    display: inline-flex;
    color: $text-color;
    padding: 20px;

    & .top-menu__icon {
      margin-right: 20px;
    }
  }

  .profil {
    padding: 20px;
    border-right: 1px solid $text-color;
    border-left: 1px solid $text-color;
  }

  .lang {
    flex: 0 0 auto;
  }

  .top-menu__social {
    display: block;
    width: 100%;
    margin: 0;
    border-top: 1px solid $text-color;
    padding: 20px;
  }

  .button--pay {
    order: 2;
    width: 100%;
    margin: 0;
  }
}
