.why {
  background-color: #f2f2f2;
  padding: 70px 0 100px;
  text-align: center;
}

.why__title {
  font-weight: normal;
  font-size: 42px;
  margin: 0 0 50px;
}

.why__list {
  list-style: none;
  padding: 0;
}

.why__item {
  max-width: 300px;
}

.why__item-title {
  font-weight: bold;
  font-size: 20px;
  text-transform: uppercase;
}

@media (max-width: $mobile-width) {
  .moneybox {
    padding: 50px 0 30px;
  }

  .moneybox__title {
    font-size: 20px;
  }
}
