.callback {
  padding: 70px 0 100px;
  background: #f1aa00;
  color: #f6f6f6;
}

.callback__title {
  font-weight: normal;
  font-size: 42px;
  max-width: 460px;
  margin: 0 auto 30px;
  text-align: center;
}

.form {
  display: flex;
}

.form__item {
  background: #f7b20e;
  border: 1px solid #fff;
  border-radius: 3px;
  padding: 40px 55px;
  max-width: 460px;
}

.form__input,
.form-textarea {
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
}

@media (max-width: $mobile-width) {
  .callback {
    padding: 50px 0 30px;
  }

  .callback__title {
    font-size: 20px;
  }
}
