.title {
  background: $bg-color url("../img/top-bg-desctop.jpg") no-repeat bottom right/100% auto;
  padding-top: 120px;
}

.title__wrapper {
  text-align: center;
  max-width: 660px;
  padding-left: 90px;
}

.title__text {
  font-size: 42px;
  line-height: 48px;
  max-width: 660px;
  margin-bottom: 30px;
  font-weight: normal;

  &::before {
    content: "";
    display: block;
    width: 40px;
    height: 28px;
    background: url("../img/cit.png") no-repeat center /contain;
    margin: 0 auto 30px;
  }
}

.title__button {
  margin-bottom: 255px;
}

@media (max-width: $mobile-width) {
  .title {
    background-image: url("../img/top-bg-mob.jpg");
    background-position: bottom 40px right;

    & .wrapper {
      padding: 0;
    }
  }

  .title__wrapper {
    padding-left: 0;
    max-width: auto;
  }

  .title__text {
    font-size: 20px;
    line-height: 26px;
    max-width: 275px;
    margin: 0 0 365px 50px;

    &::before {
      width: 21px;
      height: 15px;
    }
  }

  .title__button {
    margin-bottom: 0;
    width: 100vw;
  }
}
