.faq {
  padding: 70px 0 100px;
}

.faq__title {
  font-weight: normal;
  font-size: 42px;
  margin: 0 0 50px;
  text-align: center;
}

.faq__list {
  padding: 0;
  list-style: none;
}

.faq__item {
  padding: 20px 60px;
  margin-bottom: 10px;
  background: $bg-color url("../img/Path.png") no-repeat center right 25px;
}

.faq__item-title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
}

@media (max-width: $mobile-width) {
  .faq {
    padding: 50px 0 30px;
  }

  .faq__title {
    font-size: 20px;
  }

  .faq__item {
    padding: 10px 55px 10px 15px;
  }

  .faq__item-title {
    font-size: 15px;
  }
}
